.checks input[type="radio"],
.checks input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.checks input[type="radio"]:focus,
.checks input[type="checkbox"]:focus {
  width: 20px;
  height: 20px;
  outline: solid;
}

.checks input[type="radio"]+label {
  display: inline-block;
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  color: #333;
  min-width: 0;
  font-weight: 700;
  font-size: 15px;
  line-height: 30px;
  text-align: left;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  margin-right: 40px;
  position: relative;
}

.checks input[type="radio"]+label:before {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  content: "";
  width: 30px;
  height: 30px;
  vertical-align: middle;
  margin-right: 10px;
  background: url(data:image/png;base64,) no-repeat left top/30px;
}

.checks input[type="radio"]+label:last-of-type {
  margin: 0;
}

.checks input[type="checkbox"]+label {
  display: inline-block;
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  color: #333;
  min-width: 0;
  font-weight: 700;
  font-size: 15px;
  line-height: 30px;
  text-align: left;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.checks input[type="checkbox"]+label:before {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  content: "";
  width: 30px;
  height: 30px;
  vertical-align: top;
  margin-right: 10px;
  background: url(data:image/png;base64,) no-repeat left top/30px;
}

.checks input[type="radio"]:checked+label:before,
.checks input[type="checkbox"]:checked+label:before {
  background-position: left bottom;
}

.checks input[type="checkbox"]#check-all+label {
  display: block;
  padding-left: 0;
  color: #62c1d0;
  line-height: 45px;
  text-align: center;
  border: 1px solid #62c1d0;
  border-radius: 45px;
}

.checks input[type="checkbox"]#check-all+label:before {
  position: relative;
  display: inline-block;
  content: "";
  width: 13px;
  height: 15px;
  vertical-align: middle;
  margin-right: 10px;
  background: url(data:image/png;base64,) no-repeat left top/13px;
}

.checks input[type="checkbox"]#check-all:checked+label {
  background: #62c1d0;
  color: #fff;
}

.checks input[type="checkbox"]#check-all:checked+label:before {
  background-position: left bottom;
}

