.cert-wrap .warn { color: #e52638; padding-left: 20px; background: url(data:image/png;base64,) no-repeat left top/13px; margin-top: 10px; }
.cert-wrap .cert-num { display: grid; grid-template-columns: 7fr 3fr; -webkit-box-align: center; -ms-flex-align: center; align-items: center; margin-top: 15px; border: 1px solid #e5e5e5; }
.cert-wrap .cert-num .time { text-align: center; color: #e52638; text-align: right; padding-right: 15px; }
.cert-wrap .cert-num input { border: none; margin-top: 0; }
.certcode-wrap { padding-top: 50px; }
.certcode-wrap .text { font-size: 12px; color: #333; text-align: center; }
.certcode-wrap .textred { color: #e52638; margin-top: 10px; text-align: center; }
.certcode-wrap .pass-wrap { margin-top: 30px; display: grid; grid-column-gap: 15px; -webkit-box-pack: center; -ms-flex-pack: center; justify-content: center; grid-template-columns: 40px 40px 40px 40px; }
.certcode-wrap .pass-wrap input[type="password"] { border: none; border-bottom: 1px solid #e5e5e5; font-size: 40px; width: 100%; text-align: center; padding: 0; text-indent: 0; }
.certcode-wrap .number { display: grid; grid-column-gap: 35px; grid-row-gap: 15px; -webkit-box-pack: center; -ms-flex-pack: center; justify-content: center; margin: 30px auto 0; grid-template-columns: 75px 75px 75px; }
.certcode-wrap .number button { width: 75px; line-height: 75px; font-size: 30px; color: #333; font-weight: 700; border-radius: 100%; background: none; }
.certcode-wrap .number button:active { background: #62c1d0; color: #fff; }
.certcode-wrap .number button.del { background: url(data:image/png;base64,) no-repeat center top/75px; }
.certcode-wrap .number button.del:active { background: url(data:image/png;base64,) no-repeat center bottom #62c1d0; background-size: 75px; }
.certcode-wrap .number button.finger { background: url(data:image/png;base64,) no-repeat center top/75px; }
.certcode-wrap .number button.finger:active { background: url(data:image/png;base64,) no-repeat center bottom #62c1d0; background-size: 75px; }
.cert-finger { padding-top: 305px; background: url(/static/img/fingerprint.c8d2b94.png) no-repeat 50% 150px; background-size: 117px; }
.cert-finger p { text-align: center; font-size: 15px; }
