/*      */
.search { display: -webkit-box; display: -ms-flexbox; display: flex; margin-bottom: 15px; }
.search div { width: 50%; }
.search div.right { text-align: right; }
.search div.right button { width: 35px; height: 35px; background: url(data:image/png;base64,) no-repeat center/contain; }
.board-list { border-top: 1px solid #e5e5e5; }
.board-list .list { padding: 10px 0; border-bottom: 1px solid #e5e5e5; display: grid; grid-gap: 20px; grid-template-columns: 3fr 1fr; }
.board-list .list .left .title a { font-size: 14px; color: #222; line-height: 18px; display: -webkit-box; white-space: normal; -webkit-line-clamp: 2; word-break: break-all; overflow: hidden; text-overflow: ellipsis; -webkit-box-orient: vertical; }
.board-list .list .left .sub { margin-top: 30px; display: -webkit-box; display: -ms-flexbox; display: flex; }
.board-list .list .left .sub p { width: 50%; font-size: 12px; color: #666; }
.board-list .list .left .sub p:last-child { text-align: right; }
.board-list .list .right img { height: 80px; }
/*   My   */
.alarm-setting { padding: 30px 21px; }
.alarm-setting ul li { display: grid; grid-template-columns: 1fr 1fr; border: 1px solid #e5e5e5; padding: 20px; border-radius: 10px; margin-bottom: 15px; -webkit-box-align: center; -ms-flex-align: center; align-items: center; }
.alarm-setting ul li p:last-child { text-align: right; }
.alarm-setting ul li p em { font-size: 14px; color: #333; margin-bottom: 5px; display: block; }
.alarm-setting ul li .block { display: block; grid-column: 1 / 3; background: url(data:image/png;base64,) no-repeat right center; background-size: 7px; }
.alarm-setting ul li .block p { text-align: left; }
.mypage-top { padding: 20px 21px; display: grid; grid-template-columns: repeat(3, 1fr); -webkit-box-align: center; -ms-flex-align: center; align-items: center; border-bottom: 1px solid #e5e5e5; }
.mypage-top .key { color: #9b9489; font-weight: 700; line-height: 28px; }
.mypage-top .value { grid-column: 2 / 4; font-size: 15px; color: #222; font-weight: 700; }
.mypage-top .value.email { grid-column: 2 / 3; }
.mypage-top .link { text-align: right; }
.mypage-top .link a { color: #9b9489; border: 1px solid #9b9489; padding: 4px 15px; border-radius: 100px; font-weight: 700; }
.mypage-links { display: grid; grid-template-columns: repeat(3, 1fr); }
.mypage-links a { display: block; padding-top: 64px; text-align: center; color: #333; }
.mypage-links a:nth-child(1) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(2) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(3) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(4) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(5) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(6) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(7) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(8) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.mypage-links a:nth-child(9) { background: url(data:image/png;base64,) no-repeat 50% 30px/24px; }
.member-exit { line-height: 50px; font-size: 13px; color: #666; border-top: 1px solid #e5e5e5; text-align: center; position: fixed; left: 0; bottom: 0; width: 100%; }
.member-exit a { font-weight: 700; text-decoration: underline; }
